import mbtidata from "../json/mbtiresult";
import { useLocation,Link } from "react-router-dom";
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import { CopyToClipboard } from "react-copy-to-clipboard";
function ResultPage(params) {
    const location = useLocation();
    const str = location.state
    //str은 ESTJESTJESTJ 이런식
    //str요소마다 e,s,t,j 갯수 새서 넣음 
    let e = 0, s = 0, t = 0, j = 0;
    for (let c = 0; c <= str.length; c++) {
        if (str[c] === 'e')
            {e++;}
        else if (str[c] === 's')
            {s++;}
        else if (str[c] === 't')
            {t++;}
        else if (str[c] === 'j')
            {j++;}
    }
    // console.log(str)
    // console.log(e)
    // console.log(s)
    // console.log(t)
    // console.log(j)
    //각자 더 많은 유형을 넣어줌 
    let result = [e >= 2 ? 'e' : 'i',
    s >= 2 ? 's' : 'n',
    t >= 2 ? 't' : 'f',
    j >= 2 ? 'j' : 'p'
];

//id와 일치하는 mbti 찾아줌
let count;
outer: for (count = 0; count < 16; count++) {
    for (let i = 0; i < 4; i++) {
        if (mbtidata[count].id[i] !== result[i]) {
            break;
        } else if (i === 3) {
            break outer;
        }
    }
}
    // const s = location.state.s;
    // const t = location.state.t;
    // const j = location.state.j;
    // console.log(e)
    return (<div>
        {/* <article className="mt-5">
        <h2>당신의 연애 유형은?</h2>
        <h3>{mbtidata[count].nickname}</h3>
        <img className='mt-3' alt="image" src={mbtidata[count].img} max-width='100px'/>
        <div>
            <ul>
                <li>{mbtidata[count].description[0]}</li>
                <li>{mbtidata[count].description[1]}</li>
                <li>{mbtidata[count].description[2]}</li>
                <li>{mbtidata[count].description[3]}</li>
            </ul>
        </div>
        </article> */}
        <Card style={{ width: '18rem' }} className="card">
        <div className="starting"><h2>당신의 연애 유형은?</h2></div>
      <Card.Img variant="top" src={mbtidata[count].img} />
      <Card.Body>
        <Card.Title><h3 className="red">{mbtidata[count].nickname}</h3></Card.Title>
        <Card.Text>
                <ListGroup>
            <ListGroup.Item>{mbtidata[count].description[0]}</ListGroup.Item>
            <ListGroup.Item>{mbtidata[count].description[1]}</ListGroup.Item>
            <ListGroup.Item>{mbtidata[count].description[2]}</ListGroup.Item>
            <ListGroup.Item>{mbtidata[count].description[3]}</ListGroup.Item>
            
            </ListGroup>
        </Card.Text>
        
        <div className="btn">
	<CopyToClipboard text={window.location.toString()} onCopy={() => alert("공유 링크가 복사되었습니다!")}>
		<Button variant="danger">결과 공유하기</Button>
	</CopyToClipboard>
    <Link to="/" style={{textDecoration: 'none'}}>
		<Button variant="warning">다시 테스트 하기</Button>
	</Link>
</div>
        
      </Card.Body>
    </Card>
    </div>)
}
export default ResultPage